import React, { useState, useEffect } from "react";
import {
  AppBar, Toolbar, Typography, Box, Button, Dialog, DialogTitle,
  DialogContent, DialogActions, TextField, MenuItem, Chip, Table,
  TableHead, TableRow, TableCell, TableBody, IconButton, Autocomplete
} from "@mui/material";
import { Add as AddIcon, Edit as EditIcon, Delete as DeleteIcon } from "@mui/icons-material";

const STATUS_LIST = ["준비", "제판", "출고"];
const PAYMENT_LIST = ["미지급", "지급", "지출"];
const STATUS_COLOR = { 준비: "grey", 제판: "green", 출고: "red" };
const PAYMENT_COLOR = { 미지급: "orange", 지급: "blue", 지출: "black" };

// MUI Chips
function StatusChip({ status }) {
  return (
    <Chip
      label={status}
      sx={{
        bgcolor: STATUS_COLOR[status],
        color: "white",
        fontWeight: 700
      }}
      size="small"
    />
  );
}
function PaymentChip({ payment }) {
  return (
    <Chip
      label={payment}
      sx={{
        bgcolor: PAYMENT_COLOR[payment],
        color: "white",
        fontWeight: 700
      }}
      size="small"
    />
  );
}

// 거래처/상품 자동완성+신규등록
function AutoAddInput({ label, options, value, onChange, onAdd }) {
  const [input, setInput] = useState("");
  const showAdd = input.length > 0 && !options.some(opt => opt === input);
  return (
    <Box>
      <Autocomplete
        freeSolo
        options={options}
        value={value}
        inputValue={input}
        onInputChange={(e, val) => setInput(val)}
        onChange={(e, val) => { onChange(val); setInput(val || ""); }}
        renderInput={params => (
          <TextField {...params} label={label} variant="outlined" margin="dense" fullWidth />
        )}
      />
      {showAdd &&
        <Button
          variant="outlined"
          size="small"
          sx={{ mt: 1 }}
          onClick={() => {
            onAdd(input);
            onChange(input);
            setInput("");
          }}
        >
          "{input}" 신규등록
        </Button>
      }
    </Box>
  );
}

// 입력/수정폼 Dialog
function WorkForm({ open, onClose, onSave, customers, products, edit }) {
  const isEdit = !!edit;
  const [date, setDate] = useState(edit?.date || new Date().toISOString().slice(0, 10));
  const [customer, setCustomer] = useState(edit?.customer || "");
  const [product, setProduct] = useState(edit?.product || "");
  const [qty, setQty] = useState(edit?.qty || 1);
  const [price, setPrice] = useState(edit?.price || 0);
  const [status, setStatus] = useState(edit?.status || STATUS_LIST[0]);
  const [payment, setPayment] = useState(edit?.payment || PAYMENT_LIST[0]);
  // 단가 자동입력
  useEffect(() => {
    if (!product) return;
    const found = products.find(p => p.name === product);
    if (found) setPrice(found.price);
  }, [product, products]);
  const amount = qty * price;

  return (
    <Dialog open={open} onClose={onClose} fullWidth>
      <DialogTitle>{isEdit ? "작업 수정" : "작업 등록"}</DialogTitle>
      <DialogContent>
        <TextField
          type="date"
          label="날짜"
          value={date}
          onChange={e => setDate(e.target.value)}
          margin="dense"
          fullWidth
          InputLabelProps={{ shrink: true }}
        />
        <AutoAddInput
          label="거래처"
          options={customers.map(c => c.name)}
          value={customer}
          onChange={setCustomer}
          onAdd={name => { if (!customers.some(c => c.name === name)) customers.push({ name }); }}
        />
        <AutoAddInput
          label="작업내용"
          options={products.map(p => p.name)}
          value={product}
          onChange={setProduct}
          onAdd={name => {
            const price = Number(prompt("기본 단가를 입력하세요", "0")) || 0;
            if (!products.some(p => p.name === name)) products.push({ name, price });
            setPrice(price);
          }}
        />
        <Box sx={{ display: "flex", gap: 2 }}>
          <TextField
            label="수량"
            type="number"
            value={qty}
            onChange={e => setQty(Number(e.target.value))}
            margin="dense"
            sx={{ flex: 1 }}
          />
          <TextField
            label="단가"
            type="number"
            value={price}
            onChange={e => setPrice(Number(e.target.value))}
            margin="dense"
            sx={{ flex: 1 }}
          />
          <TextField
            label="금액"
            type="number"
            value={amount}
            InputProps={{ readOnly: true }}
            margin="dense"
            sx={{ flex: 1, bgcolor: "#f5f5f5" }}
          />
        </Box>
        <Box sx={{ display: "flex", gap: 2, mt: 1 }}>
          <TextField
            select label="상태"
            value={status}
            onChange={e => setStatus(e.target.value)}
            margin="dense"
            sx={{ flex: 1 }}
          >
            {STATUS_LIST.map(s => <MenuItem key={s} value={s}>{s}</MenuItem>)}
          </TextField>
          <TextField
            select label="수입"
            value={payment}
            onChange={e => setPayment(e.target.value)}
            margin="dense"
            sx={{ flex: 1 }}
          >
            {PAYMENT_LIST.map(s => <MenuItem key={s} value={s}>{s}</MenuItem>)}
          </TextField>
        </Box>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>취소</Button>
        <Button
          onClick={() => {
            if (!customer || !product) return alert("거래처, 작업내용 입력");
            onSave({
              date, customer, product, qty, price, amount, status, payment
            });
            onClose();
          }}
          variant="contained"
        >저장</Button>
      </DialogActions>
    </Dialog>
  );
}

export default function App() {
  // 데이터
  const [works, setWorks] = useState([]);
  const [customers, setCustomers] = useState([]);
  const [products, setProducts] = useState([]);
  const [tab, setTab] = useState("진행중");
  const [formOpen, setFormOpen] = useState(false);
  const [editIdx, setEditIdx] = useState(null);

  // 로컬스토리지 연동
  useEffect(() => {
    const w = localStorage.getItem("works"); if (w) setWorks(JSON.parse(w));
    const c = localStorage.getItem("customers"); if (c) setCustomers(JSON.parse(c));
    const p = localStorage.getItem("products"); if (p) setProducts(JSON.parse(p));
  }, []);
  useEffect(() => { localStorage.setItem("works", JSON.stringify(works)); }, [works]);
  useEffect(() => { localStorage.setItem("customers", JSON.stringify(customers)); }, [customers]);
  useEffect(() => { localStorage.setItem("products", JSON.stringify(products)); }, [products]);

  // 탭별 리스트
  const filtered = works.filter(w => {
    if (tab === "진행중") return w.status !== "출고";
    if (tab === "완료") return w.status === "출고";
    if (tab === "미지급") return w.payment === "미지급";
    return true;
  });

  // 미지급 합계
  const unpaidSum = works.filter(w => w.payment === "미지급").reduce((a, b) => a + b.amount, 0);

  // 폼(신규/수정)
  function openForm(idx = null) { setEditIdx(idx); setFormOpen(true); }

  // 저장
  function handleSave(data) {
    if (editIdx === null) setWorks([...works, { ...data, id: Date.now() }]);
    else setWorks(works.map((w, i) => (i === editIdx ? { ...w, id: w.id } : w)));
    // 거래처/상품 신규등록 자동추가(중복체크)
    if (!customers.some(c => c.name === data.customer))
      setCustomers([...customers, { name: data.customer }]);
    if (!products.some(p => p.name === data.product))
      setProducts([...products, { name: data.product, price: data.price }]);
  }

  // 삭제
  function handleRemove(idx) {
    if (window.confirm("삭제하시겠습니까?")) {
      setWorks(works.filter((_, i) => i !== idx));
    }
  }

  // 바로수정
  function quickEdit(idx, key, value) {
    setWorks(works => works.map((w, i) =>
      i === idx ? { ...w, [key]: value, amount: key === "qty" || key === "price" ? (key === "qty" ? value : w.qty) * (key === "price" ? value : w.price) : w.amount } : w
    ));
  }

  // 탭 목록
  const tabs = ["진행중", "완료", "미지급"];

  return (
    <Box sx={{ bgcolor: "#f7f8fa", minHeight: "100vh" }}>
      {/* 상단 AppBar */}
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" sx={{ flexGrow: 1 }}>매출·작업 관리</Typography>
          <Button color="inherit" startIcon={<AddIcon />} onClick={() => openForm(null)}>
            등록
          </Button>
        </Toolbar>
      </AppBar>
      {/* 탭 */}
      <Box sx={{ display: "flex", mt: 1, mb: 1 }}>
        {tabs.map(t =>
          <Button
            key={t}
            onClick={() => setTab(t)}
            variant={tab === t ? "contained" : "text"}
            sx={{ flex: 1, fontWeight: 700 }}
          >{t}</Button>
        )}
      </Box>
      {/* 리스트 */}
      <Box sx={{ mx: { xs: 0.5, sm: 4 } }}>
        <Table size="small" sx={{ bgcolor: "#fff" }}>
          <TableHead>
            <TableRow>
              <TableCell>날짜</TableCell>
              <TableCell>거래처</TableCell>
              <TableCell>작업내용</TableCell>
              <TableCell>수량</TableCell>
              <TableCell>단가</TableCell>
              <TableCell>금액</TableCell>
              <TableCell>상태</TableCell>
              <TableCell>수입</TableCell>
              <TableCell align="right"></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {filtered.map((w, idx) => (
              <TableRow key={w.id}>
                <TableCell>{w.date}</TableCell>
                <TableCell>{w.customer}</TableCell>
                <TableCell>{w.product}</TableCell>
                <TableCell>
                  <TextField
                    value={w.qty}
                    type="number"
                    size="small"
                    sx={{ width: 60 }}
                    onChange={e => quickEdit(works.findIndex(x => x.id === w.id), "qty", Number(e.target.value))}
                  />
                </TableCell>
                <TableCell>
                  <TextField
                    value={w.price}
                    type="number"
                    size="small"
                    sx={{ width: 80 }}
                    onChange={e => quickEdit(works.findIndex(x => x.id === w.id), "price", Number(e.target.value))}
                  />
                </TableCell>
                <TableCell>{w.amount.toLocaleString()}</TableCell>
                <TableCell>
                  <TextField
                    select
                    value={w.status}
                    size="small"
                    sx={{ width: 70, bgcolor: STATUS_COLOR[w.status], color: "#fff", borderRadius: 1 }}
                    onChange={e => quickEdit(works.findIndex(x => x.id === w.id), "status", e.target.value)}
                  >
                    {STATUS_LIST.map(s => <MenuItem key={s} value={s}>{s}</MenuItem>)}
                  </TextField>
                </TableCell>
                <TableCell>
                  <TextField
                    select
                    value={w.payment}
                    size="small"
                    sx={{ width: 70, bgcolor: PAYMENT_COLOR[w.payment], color: "#fff", borderRadius: 1 }}
                    onChange={e => quickEdit(works.findIndex(x => x.id === w.id), "payment", e.target.value)}
                  >
                    {PAYMENT_LIST.map(s => <MenuItem key={s} value={s}>{s}</MenuItem>)}
                  </TextField>
                </TableCell>
                <TableCell align="right">
                  <IconButton size="small" onClick={() => openForm(works.findIndex(x => x.id === w.id))}>
                    <EditIcon fontSize="small" />
                  </IconButton>
                  <IconButton size="small" color="error" onClick={() => handleRemove(works.findIndex(x => x.id === w.id))}>
                    <DeleteIcon fontSize="small" />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
            {filtered.length === 0 &&
              <TableRow>
                <TableCell colSpan={9} align="center" sx={{ color: "#888" }}>자료 없음</TableCell>
              </TableRow>
            }
          </TableBody>
        </Table>
      </Box>
      {/* 미지급 합계 표시 */}
      {tab === "미지급" &&
        <Box sx={{ textAlign: "right", fontWeight: 700, m: 2 }}>
          미지급 합계: {unpaidSum.toLocaleString()} 원
        </Box>
      }
      {/* 입력/수정폼 */}
      <WorkForm
        open={formOpen}
        onClose={() => setFormOpen(false)}
        onSave={handleSave}
        customers={customers}
        products={products}
        edit={editIdx !== null ? works[editIdx] : null}
      />
    </Box>
  );
}
